<template>
    
     <div class="navbar" :class="{sticky_nav: scrollPosition > 20}">
        <div class = "max-width">
            <div class = "logo">
             <!--   <div>
                 <img class = logokcp src="https://icon2.cleanpng.com/20180809/hco/kisspng-clip-art-feather-image-borders-and-frames-portable-kalem-hokka-ve-lekeler-png-kalem-mrekkep-resim-5b6c11ccf3f419.5007924715338091009992.jpg">
                </div>-->
                <div class = "portfolio" @click="$emit('scroll-component', 'Home')">Portfolio.</div>    
            </div>

            <ul class="menu" :class="{active: isActive}">
                <li><p @click="sampleMethod('Home')">HOME</p></li>
                <li><p @click="sampleMethod('About')">ABOUT</p></li>
                <li><p @click="sampleMethod('Service')">SERVICE</p></li>
                <li><p @click="sampleMethod('Contact')">CONTACT</p></li>
            </ul>
            <div class="menu-btn"  @click="menuBtn">
                    <img class="menu-img" src="https://icon-library.com/images/white-menu-icon-png/white-menu-icon-png-18.jpg">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'Heading',
    data() {
        return {
            scrollPosition: null,
            isActive: false
        }
    },
    components:{
   
     },
    methods:{
        
        sampleMethod(x){
            this.$emit("scroll-component", document.getElementsByClassName(x)[0].getBoundingClientRect().y);
            if(window.screen.width < 500)
            this.isActive = !this.isActive;
        },

        updateScroll() {
            this.scrollPosition = window.scrollY
        },
        menuBtn(){
    //         /* eslint-disable no-debugger */
    //   debugger;
            this.isActive = !this.isActive;
        }
    },
    mounted() {
        window.addEventListener('scroll', this.updateScroll);
    }
}



//  document.ready(function(){
//     window.scroll(function(){
//         if(this.scrollY>20){
//             ('.navbar').addClass("sticky");
//         }
//     })
// });
</script>

<style scoped>

.navbar{
    position:fixed;
    margin-top: 0%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    width: 100%;
    max-width: 1024px;
    align-items: center;
    margin: auto;
    left: 50%;
    top: 0%;
    transform: translate(-50%, -0%);
}

.sticky_nav {
    background-color: crimson;
    transition: color 0.3s ease;
}

.max-width{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    
}


.navbar .logo{
   margin-left: 20px;
   margin-top: 5px;
   display: flex;
  
}

.navbar .logo .logokcp{
    width: 40px;
    height: 40px;
    padding: 5px 9px;
    border-radius: 25%;
    
}

.navbar .logo .portfolio{
    display: flex;
    color: #f5f5f5;
    font-size: 40px;
    font-weight: 600;
}

.navbar .logo .portfolio:hover{
    color:red;
    cursor: pointer;
}

.navbar .menu li{
    list-style: none;
    display: inline-block;
}


.navbar .menu li p{
    color:#f5f5f5;
    font-size: 18px;
    font-weight: 500;
    margin-right: 50px;
    transition: color 0.3s ease;
    
}

.navbar .menu li p:hover{
    color:red;
    cursor: pointer;
}

.navbar .menu-btn{
    cursor: pointer;
    display:none;
}

.navbar .menu-btn .menu-img{
    width: 40px;
    height: 40px;
    padding: 20px 40px;
    border-radius: 25%;
}

@media (max-width: 500px){
    .navbar .menu-btn{
        cursor: pointer;
        display:block;
        z-index: 999;
    }

    .menu{
        position: fixed;
        height: 100kvh;
        width: 100%;
        margin: 0;
        left: -120%;
        top: 0;
        background: black;
        text-align: center;
        padding-top: 80px;
        transition: all 0.3s ease;
    }

    .active{
       left: 0;
    }

    .navbar .menu li{
        display: block;
    }   

    .navbar .menu li p{
        display: inline-block;
        margin: 20px 20;
        font-size: 25px;
    }   


}

</style>